.chat {
    width: 95%;
    margin: 0 auto;
}
footer{
    display: flex;
    align-items: center;
    flex-direction: column;
}
footer > ul {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    padding: 0;
}
footer>ul>li{
    margin: 0.5rem 1.5rem;
    font-size: 18px;
    font-weight: bold;
    color: white;
}
footer> p{
    color: white;
    font-size: 16px;
}