p {
    margin: 0;
}

.ava {
    padding: 0.5rem;
}

.message {
    border-radius: 10px;
    background-color: var(--gray);
    position: relative;
    margin: 1rem 1rem 1rem 0;
    display: flex;
    padding: 0.5rem;
    height: 100px;
    width: 55%;
}

.message.owner {
    margin: 1rem 0 1rem auto;
    background-color: var(--darkGray);
}

.message .delete, .edit {
    background-color: transparent;
    position: absolute;
    bottom: 1rem;
    display: none;
    color: white;
    border: none;
    line-height: 0;
}

.owner .delete {
    display: inline;
}

.owner .edit {
    display: inline;
}

.delete {
    font-size: 18px;
    right: 1rem;
}

.edit {
    right: 3rem;
}

.message.owner > .like {
    display: none;
}

.owner > .textBlock > .messageText {
    color: white;
}
.textBlock>.messageText>input{
    border: none;
    outline: none;
}
.like {
    padding: 0;
    border: none;
    width: 25px;
    height: 25px;
    background-color: transparent;
    position: absolute;
    bottom: 0.5rem;
    right: 0.5rem;
}

.like > img {
    width: 100%;
    height: 100%;
}

.date {
    top: 0.5rem;
    right: 0.5rem;
    position: absolute;
    color: var(--darkGray);
    filter: contrast(0.2);
}

.name {
    color: var(--green);
    font-size: 20px;
    font-weight: bold;
}